#main-bg {
  width: 100%;
  height: 100vh;
}

.video-container {
  width: 100%;
  height: 100vh;
}

.video-container video {
  width: 100%;
  height: 100vh;
  -o-object-fit: cover;
  object-fit: cover;
}

.shadow {
  height: 100vh;
  width: 100%;
  background: rgba(14, 13, 21, 0.3);
  position: absolute;
  z-index: 102;
}

.mobile-video {
  display: block;
}

.mobile-video.inactive {
  display: none;
}

@media (max-width: 500px) {
  #main-bg {
    height: initial;
  }

  .video-container {
    height: initial;
  }

  .video-container video {
    height: initial;
  }

  .shadow {
    display: none;
  }
}
