#preloader {
  height: 100vh;
  width: 100%;
  background: #0e0d15;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 9999999999999999;
  transition: 1s;
}

.preloader-img__logo {
  position: absolute;
}

.preloader-img {
  -webkit-animation: 1.6s linear 0s normal none infinite running preloader-img;
  animation: 1.6s linear 0s normal none infinite running preloader-img;
}

@-webkit-keyframes preloader-img {
  0% {
    transform: scale(1, 1);
  }

  50% {
    transform: scale(-1, 1);
  }

  100% {
    transform: scale(1, 1);
  }
}

@keyframes preloader-img {
  0% {
    transform: scale(1, 1);
  }

  50% {
    transform: scale(-1, 1);
  }

  100% {
    transform: scale(1, 1);
  }
}
