#restauran-bg {
  width: 100%;
  height: 100vh;
  position: relative;
  background: #0e0d15;
}

.restauran-video-container {
  width: 100%;
  height: 100vh;
}

.restauran-video-container video {
  width: 100%;
  height: 100vh;
  -o-object-fit: cover;
  object-fit: cover;
}

.rest-shadow {
  background: linear-gradient(181.35deg, rgba(0, 0, 0, 0.44) 15.11%, rgba(0, 0, 0, 0) 98.85%);
  width: 100%;
  height: 150vh;
  position: absolute;
}

.restauran {
  position: absolute;
  bottom: 60px;
  z-index: 102;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.restauran-title {
  font-family: 'Playfair Display', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 54px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #ecbd81;
  margin-bottom: 40px;
}

.restauran__description {
  display: flex;
}

.restauran__description__item {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  color: #fff;
  display: flex;
  align-items: center;
  padding: 0 50px;
}

.restauran__description__item:first-child {
  text-align: right;
}

.restauran__description__item:nth-child(2) {
  text-align: center;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
}

.restauran__description__item:last-child {
  text-align: left;
}

@media (max-width: 576px) {
  .restauran {
    position: static;
    margin-bottom: 60px;
  }

  .restauran__description__item {
    font-size: 14px;
    padding: 0 26px;
  }

  .rest-shadow {
    display: none;
  }
}

@media (max-width: 500px) {
  .restauran-video-container {
    height: initial;
  }

  .restauran-video-container video {
    height: initial;
  }
}

@media (max-width: 470px) {
  .restauran__description__item {
    font-size: 14px;
    padding: 0 10px;
  }
}

@media (max-width: 370px) {
  .restauran__description__item {
    font-size: 12px;
    padding: 0 10px;
  }
}

@media (max-height: 450px) {
  .restauran {
    bottom: 20px;
  }

  .restauran__description__item {
    font-size: 14px;
    line-height: 130%;
  }

  .restauran-title {
    font-size: 36px;
    margin-bottom: 25px;
  }
}
