#photo-gallery {
  background: #fff;
  padding-top: 160px;
}

.photo-gallery-inner {
  position: relative;
}

.photo-gallery img {
  width: 100%;
  height: 374px;
  -o-object-fit: cover;
  object-fit: cover;
}

.gallery__arrow {
  height: 50px;
  width: 50px;
  cursor: pointer;
  background-color: rgba(237, 237, 237, 0.7);
  border: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  position: absolute;
  top: 42%;
  z-index: 1;
}

.gallery__arrow img {
  height: 16px;
  width: 8px;
}

.gallery__arrow--prev {
  left: 27.5vw;
}

.gallery__arrow--next {
  right: 27.4vw;
}

@media (max-width: 1700px) {
  .gallery__arrow--prev {
    left: 27vw;
  }

  .gallery__arrow--next {
    right: 27vw;
  }
}

@media (max-width: 1600px) {
  .photo-gallery img {
    height: 270px;
    -o-object-fit: cover;
    object-fit: cover;
  }
}

@media (max-width: 1500px) {
  .gallery__arrow--prev {
    left: 26vw;
  }

  .gallery__arrow--next {
    right: 26vw;
  }
}

@media (max-width: 1200px) {
  .gallery__arrow--prev {
    left: 22vw;
  }

  .gallery__arrow--next {
    right: 22vw;
  }
}

@media (max-width: 1050px) {
  #photo-gallery {
    padding: 40px 20px;
  }

  .photo-gallery {
    text-align: center;
  }

  .gallery__arrow--prev {
    left: 17vw;
  }

  .gallery__arrow--next {
    right: 17vw;
  }
}

@media (max-width: 900px) {
  #photo-gallery {
    margin-top: 100px;
    padding: 0 20px 40px;
  }

  .gallery__arrow--prev {
    left: 16vw;
  }

  .gallery__arrow--next {
    right: 16vw;
  }
}

@media (max-width: 768px) {
  .gallery__arrow--prev {
    left: 12vw;
  }

  .gallery__arrow--next {
    right: 12vw;
  }
}

@media (max-width: 576px) {
  #photo-gallery {
    margin-top: 20px;
  }

  .gallery__arrow--prev {
    left: 2vw;
  }

  .gallery__arrow--next {
    right: 2vw;
  }
}

@media (max-height: 450px) {
  #photo-gallery {
    margin-top: 100px;
    padding: 0 20px 40px;
  }

  .mobile-map-wrapper {
    display: block;
  }
}
