html {
  height: 100%; // Footer at bottom
  font-size: 16px;
  overflow-x: hidden;
}

body {
  display: flex; // Footer at bottom
  flex-direction: column; // Footer at bottom
  height: 100%; // Footer at bottom
  color: #000;
  font-size: 1rem;
  line-height: 1.6rem;
  background: #fff;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
}

.header {
  flex-shrink: 0; // Footer at bottom
}

.main {
  position: relative;
  flex: 1 0 auto; // Footer at bottom
}

.footer {
  z-index: 10;
  flex-shrink: 0; // Footer at bottom
  margin-top: auto;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: none;

  table & {
    max-width: none;
  }
}

.d-none {
  display: none;
}
