#tab-block {
  background: #0e0d15;
  padding-bottom: 160px;
  position: relative;
}

.casino-tab-header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.casino-header-tab {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 180%;
  text-align: center;
  text-transform: uppercase;
  color: #a4a7ab;
  padding: 10px 80px;
  cursor: pointer;
}

.casino-header-tab.active {
  color: #fff;
  border-bottom: 1px solid #fff;
}

.tab-video-container {
  width: 100%;
  height: 100vh;
}

.tab-video-container video {
  width: 100%;
  height: 100vh;
  -o-object-fit: cover;
  object-fit: cover;
}

.casino-tab-games-buttons {
  display: none;
  flex-wrap: wrap;
  margin-top: 40px;
}

.casino-tab {
  margin-top: 40px;
}

.casino-tab-games-buttons__item {
  display: flex;
  flex-basis: 100%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.btn-game {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 90%;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: rgba(163, 166, 171, 0.7);
  border: 1px solid rgba(224, 224, 224, 0.1);
  background: rgba(224, 224, 224, 0.1);
  padding: 20px 0;
  width: 278px;
  margin-right: 30px;
  margin-bottom: 22px;
  position: relative;
}

.btn-game.active {
  color: #fff;
}

.btn-game-popup {
  position: absolute;
  background-color: #1c1b23;
  width: 400px;
  height: 220px;
  padding: 32px 24px;
  // z-index: 105;
  top: -250px;
  left: -50px;
  cursor: default;
  opacity: 0;
  transition: 0.35s;
  z-index: -1;
}

.btn-game-popup.active {
  opacity: 1;
  z-index: 9999;
}

.btn-game-popup__header {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.btn-game-popup__title {
  font-weight: bold;
  font-size: 20px;
  line-height: 150%;
  text-align: center;
  letter-spacing: 0.04em;
  color: #fff;
}

.btn-game-popup__close {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.06);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: absolute;
  right: 0;
}

.btn-game-popup__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn-game-popup__contnet-item {
  width: 165px;
}

.btn-game-popup__content-title {
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  color: #fff;
}

.btn-game-popup__content-title::after {
  content: '';
  display: block;
  height: 1px;
  width: inherit;
  background: #fff;
  margin: 8px 0;
}

.btn-game-popup__content-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.btn-game-popup__content-wrapper_item-title {
  font-weight: 500;
  font-size: 14px;
  line-height: 115%;
  text-align: center;
  text-transform: initial;
  color: #fff;
  margin-bottom: 16px;
}

.btn-game-popup__content-wrapper_item p {
  font-weight: normal;
  font-size: 14px;
  line-height: 115%;
  text-align: center;
  color: #a4a7ab;
  margin-bottom: 8px;
}

.casino-tab__description {
  display: flex;
  justify-content: start;
  width: 100%;
  padding: 0 200px;
  position: absolute;
  bottom: 190px;
}

.casino-tab__description__item {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  color: #fff;
  display: flex;
  align-items: center;
  margin-right: 132px;
  padding-right: 132px;
  border-right: 1px solid #fff;
  margin-bottom: 50px;
}

.casino-tab__cards-games {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

@media (min-width: 2000px) {
  .casino-tab__description {
    display: flex;
    justify-content: start;
    width: 100%;
    padding: 0 200px;
    margin: 0 auto;
    max-width: 1900px;
    margin-top: 40px;
  }

  .casino-tab__description__item {
    max-width: 340px;
  }
}

@media (max-width: 1650px) {
  .casino-tab__description__item {
    margin-right: 100px;
    padding-right: 100px;
  }
}

@media (max-width: 1450px) {
  .casino-tab__description__item {
    margin-right: 50px;
    padding-right: 50px;
  }
}

@media (max-width: 1150px) {
  .casino-tab__description {
    padding: 0 100px;
  }
}

@media (max-width: 992px) {
  #tab-block {
    background: #0e0d15;
    padding-bottom: 40px;
    padding-top: 0;
  }

  .casino-tab__description {
    padding: 0 50px;
    bottom: 50px;
  }

  .casino-tab__description__item {
    margin-right: 20px;
    padding-right: 20px;
  }

  .casino-header-tab:last-child {
    margin-top: 20px;
  }
}

@media (max-width: 576px) {
  .casino-tab-header {
    flex-direction: column;
  }

  .casino-header-tab:last-child {
    margin-top: 5px;
  }

  .casino-header-tab {
    max-width: 370px;
    padding: 10px;
  }

  .casino-tab__description {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    bottom: 55px;
  }

  .casino-tab__description__item {
    margin-right: 0;
    padding-right: 0;
    border-right: none;
    padding-bottom: 20px;
    border-bottom: 1px solid #fff;
    margin-bottom: 20px;
    font-size: 18px;
  }

  .btn-game {
    margin-right: 0;
  }

  .btn-game-popup {
    width: 355px;
    left: -39px;
  }

  .btn-game-popup__contnet-item {
    width: 150px;
  }

  .mobile-games__tab {
    flex-direction: row;
    justify-content: space-around;
  }

  .mobile-games__tab-item {
    margin-right: 20px;
  }
}

@media (max-width: 500px) {
  .tab-video-container {
    height: initial;
  }

  .tab-video-container video {
    height: initial;
  }
}
