.play-popup {
  position: fixed;
  right: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  height: 100vh;
  transition: 1s;
  z-index: 999;
  display: none;
  align-items: center;
  justify-content: center;
}

.play-popup.active {
  display: flex;
}

.play-popup-content {
  position: fixed;
  background-color: #0e0d15;
  display: flex;
  flex-direction: column;
  z-index: 999;
  transition: 1s;
  padding: 30px;
}

.play-popup-content.visible {
  transform: translateX(0);
}

.close-play-popup {
  display: flex;
  justify-content: end;
  cursor: pointer;
}

.popup-play__title {
  font-weight: normal;
  font-size: 38px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  margin-top: 26px;
}

.play-popup-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
  padding: 0 50px;
}

.play-popup-form .play-popup__input {
  width: 379px;
  border: none;
  border-bottom: 1px solid #a4a7ab;
  padding: 6px 0;
  margin-top: 40px;
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  letter-spacing: 0.02em;
  color: #fff;
  background: transparent;
}

.play-popup-form .play-popup__input::-moz-placeholder {
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  letter-spacing: 0.02em;
  text-transform: initial;
  color: #a4a7ab;
}

.play-popup-form .play-popup__input:-ms-input-placeholder {
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  letter-spacing: 0.02em;
  text-transform: initial;
  color: #a4a7ab;
}

.play-popup-form .play-popup__input::placeholder {
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  letter-spacing: 0.02em;
  text-transform: initial;
  color: #a4a7ab;
}

.play-popup-form .play-popup-policy {
  font-weight: 300;
  font-size: 14px;
  line-height: 160%;
  color: #a4a7ab;
  max-width: 379px;
  margin-top: 40px;
}

.play-popup-form .play-popup__link {
  color: #fff;
}

.play-popup-form .play-popup__btn {
  border: none;
  background: #ecbd81;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #0e0d15;
  margin-top: 24px;
  width: 100%;
  padding: 11px 0;
  margin-bottom: 80px;
}

.pure-material-textfield-outlined {
  position: relative;
  display: inline-block;
  padding-top: 5px;
  overflow: hidden;
}

.pure-material-textfield-outlined > input + span {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  max-height: 100%;
  font-size: 14px;
  line-height: 77px;
  cursor: text;
  transition: color 0.2s, font-size 0.2s, line-height 0.2s;
  color: #a4a7ab;
}

/* eslint-disable-next-line */
input:invalid + span {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

/* eslint-disable-next-line */
input:focus:invalid + span {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

.pure-material-textfield-outlined > input:not(:focus):-moz-placeholder-shown + span {
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.02em;
  text-transform: initial;
  color: #a4a7ab;
  line-height: 120px;
  padding: 3px;
  left: 0;
}

.pure-material-textfield-outlined > input:not(:focus):-ms-input-placeholder + span {
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.02em;
  text-transform: initial;
  color: #a4a7ab;
  line-height: 120px;
  padding: 3px;
  left: 0;
}

.pure-material-textfield-outlined > input:not(:focus):placeholder-shown + span {
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.02em;
  text-transform: initial;
  color: #a4a7ab;
  line-height: 120px;
  padding: 3px;
  left: 0;
}

/* eslint-disable-next-line */
input:focus:required:invalid + span {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

input:invalid {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

input:focus:invalid {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

.submit-title {
  margin-top: 100px;
}

.submit-text {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 388px;
  margin-bottom: 25px;
}

.close-play-popup__submit {
  display: none;
}

.warning-form {
  font-weight: normal;
  font-size: 12px;
  line-height: 130%;
  letter-spacing: 0.02em;
  color: #c81947;
  width: 100%;
  margin-top: 5px;
  display: none;
}

@media (max-width: 576px) {
  .play-popup-content {
    width: 100%;
  }

  .play-popup-form {
    padding: 0;
  }

  .play-popup-form .play-popup__input {
    max-width: 330px;
  }

  .play-popup-form .play-popup-policy {
    max-width: 330px;
  }

  .play-popup__btn {
    max-width: 330px;
  }

  .warning-form {
    max-width: 330px;
  }
}

@media (max-width: 380px) {
  .play-popup-form .play-popup__input {
    max-width: 250px;
    margin-top: 35px;
  }

  .play-popup-form .play-popup-policy {
    max-width: 250px;
  }

  .play-popup-form {
    margin-top: 0;
  }

  .play-popup__btn {
    max-width: 250px;
  }

  .popup-play__title {
    margin-top: 90px;
  }

  .close-play-popup {
    position: absolute;
    right: 40px;
    top: 66px;
  }
}

@media (max-height: 450px) {
  .popup-play__title {
    font-size: 20px;
    margin-top: 5px;
  }

  .play-popup-form {
    margin-top: 0;
  }

  .pure-material-textfield-outlined > input + span {
    line-height: 100px;
  }

  .pure-material-textfield-outlined > input:not(:focus):-moz-placeholder-shown + span {
    line-height: 45px;
    opacity: 1;
    font-size: 12px;
  }

  .pure-material-textfield-outlined > input:not(:focus):-ms-input-placeholder + span {
    line-height: 45px;
    opacity: 1;
    font-size: 12px;
  }

  .pure-material-textfield-outlined > input:not(:focus):placeholder-shown + span {
    line-height: 45px;
    opacity: 1;
    font-size: 12px;
  }

  .play-popup-form .play-popup__input {
    font-size: 12px;
    line-height: 100%;
    margin-top: 12px;
  }

  .play-popup-form .play-popup-policy {
    font-size: 10px;
    line-height: 110%;
    margin-top: 5px;
  }

  .play-popup-form .play-popup__btn {
    font-size: 12px;
    line-height: 8px;
    margin-top: 5px;
    margin-bottom: 30px;
  }

  .close-play-popup img {
    height: 20px;
  }
}

@media (max-width: 375px) {
  .warning-form {
    max-width: 246px;
  }
}
