.footer {
  background: #0e0d15;
  padding: 50px;
}

.footer .container {
  position: relative;
  width: 100%;
}

.footer-menu {
  display: flex;
  align-items: center;
}

.footer-menu-logo {
  position: absolute;
}

.social-media-contacts {
  position: absolute;
  right: 0;
}

.contacts-link {
  text-decoration: none;
  margin-left: 20px;
}

.footer-phone {
  position: relative;
  margin-top: 20px;
}

.footer-phone-link {
  position: absolute;
  right: 0;
  text-decoration: none;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  text-align: right;
  color: #ecbd81;
}

.footer-adress {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 60px;
}

.lang-btn.active-footer {
  color: #ecbd81;
}

.footter-adress-content__text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 160%;
  text-align: right;
  color: #fff;
}

.footer-policy {
  display: flex;
  position: relative;
  margin-top: 17px;
}

.footer-policy__content {
  position: absolute;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  color: #535353;
  width: 250px;
}

.footer-policy__registration {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  text-align: center;
  color: #8d8d8d;
  max-width: 752px;
  margin: 0 auto;
}

.footer-policy__development-link {
  position: absolute;
  right: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  text-align: right;
  color: #535353;
  text-decoration: none;
}

.footer-policy__development-link img {
  margin-left: 16px;
}

.footer-policy__content-link {
  text-decoration: none;
  color: #535353;
}

.footer-policy__content-link:hover {
  color: #fff;
}

.footer-policy__development-link:hover {
  color: #fff;
}

.lang-btn-footer:hover {
  color: #ecbd81 !important;
}

@media (max-width: 1450px) {
  .footer-policy {
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .footer-policy__registration {
    flex-basis: 100%;
    order: 1;
    margin-top: 20px;
  }

  .footer-policy__development-link {
    position: static;
  }

  .footer-policy__content {
    position: static;
  }
}

@media (max-width: 1200px) {
  .footer-navbar {
    display: flex !important;
  }

  .footer-nav-link {
    margin-right: 5px;
  }
}

@media (max-width: 880px) {
  .footer-navbar {
    flex-basis: 100%;
    flex-direction: column;
    position: relative;
    top: 100px;
  }

  .footer-nav-link {
    padding: 0 !important;
    text-transform: uppercase;
    width: 100px;
  }

  .footer-social-media-contacts {
    top: 235px;
  }

  .footer-lang {
    position: absolute;
    top: 90px;
    right: 0;
  }

  .footer-adress {
    justify-content: flex-end;
    margin-bottom: 70px;
  }
}

@media (max-width: 666px) {
  .footer-policy {
    justify-content: center;
  }

  .footer-policy__development {
    margin-top: 20px;
  }

  .footer-policy__content {
    text-align: center;
  }
}

@media (max-width: 576px) {
  .footer {
    padding: 20px;
  }
}

@media (max-width: 380px) {
  .footer-policy__development-link img {
    margin-left: 5px;
  }
}
