#hotel-block {
  background: #0e0d15;
  padding: 250px 0 160px 0;
  position: relative;
}

.hotel-title {
  font-family: 'Playfair Display', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 45px;
  line-height: 130%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  max-width: 1170px;
  margin: 0 auto;
  position: relative;
}

.hotel-title span {
  color: #ecbd81;
  font-size: 54px;
}

.hotel-inner {
  margin-top: 70px;
  position: relative;
}

.hotel-subtitle__block {
  font-weight: normal;
  font-size: 20px;
  line-height: 180%;
  color: #fff;
  width: 345px;
  position: absolute;
  right: 19vw;
  z-index: 105;
  background-color: #0e0d15;
}

.hotel-subtitle__block::before {
  content: '';
  display: block;
  width: inherit;
  height: 1px;
  margin-bottom: 40px;
  background-color: #fff;
}

.swiper-wrapper {
  display: flex;
  align-items: center;
}

.hotel__slider {
  height: 504px;
}

.hotel__slide {
  height: 240px;
  opacity: 0.4;
  transition: all 1s;
}

.hotel__slide img {
  width: inherit;
  height: 100%;
  -o-object-fit: contain;
  object-fit: contain;
}

.hotel__slide.swiper-slide-active {
  opacity: 1;
  height: 504px;
  width: 790px !important;
  -o-object-fit: cover;
  object-fit: cover;
  transition: all 1s;
}

.hotel__slide.swiper-slide-active img {
  opacity: 1;
  height: 504px;
  width: 790px !important;
  transition: 1s;
}

.hotel__arrow {
  height: 50px;
  width: 50px;
  cursor: pointer;
  background-color: rgba(237, 237, 237, 0.7);
  border: 1px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  position: absolute;
  top: 46%;
  z-index: 1;
}

.hotel__arrow--next {
  left: 57vw;
}

.hotel__arrow--prev {
  left: 21vw;
}

.about-blur__hotel {
  width: 350px;
  height: 350px;
  top: 130px;
  left: 9%;
}

@media (min-width: 2400px) {
  .hotel__arrow--prev {
    left: 17vw;
  }

  .hotel__arrow--next {
    left: 43vw;
  }
}

@media (max-width: 1800px) {
  .hotel-subtitle__block {
    right: 16vw;
  }

  .hotel__arrow--prev {
    left: 17vw;
  }

  .hotel__arrow--next {
    left: 57vw;
  }
}

@media (max-width: 1700px) {
  .hotel-subtitle__block {
    right: 10vw;
  }

  .hotel__arrow--prev {
    left: 17vw;
  }

  .hotel__arrow--next {
    left: 57vw;
  }
}

@media (max-width: 1450px) {
  .hotel__arrow--prev {
    left: 2vw;
  }

  .hotel__arrow--next {
    left: initial;
    right: 45vw;
  }
}

@media (max-width: 1300px) {
  #hotel-block {
    padding: 250px 40px 160px 40px;
  }

  .hotel__arrow--next {
    left: initial;
    right: 33vw;
  }

  .hotel-subtitle__block {
    position: static;
    margin-bottom: 20px;
    background: transparent;
  }

  .hotel-subtitle__block::before {
    margin-bottom: 20px;
  }
}

@media (max-width: 1560px) {
  #hotel-block {
    padding: 95px 0 160px 0;
  }
}

@media (max-width: 1200px) {
  .hotel__arrow--next {
    left: initial;
    right: 30vw;
  }

  .hotel-title span {
    display: block;
  }
}

@media (max-width: 1100px) {
  .hotel__arrow--next {
    left: initial;
    right: 19vw;
  }
}

@media (max-width: 1000px) {
  .hotel__arrow--next {
    left: initial;
    right: 7vw;
  }

  .hotel__slide {
    height: auto;
    width: 100% !important;
    transition: none;
  }

  .hotel__slide img {
    height: auto;
    width: 100% !important;
    transition: none;
  }

  .hotel__slide.swiper-slide-active {
    opacity: 1;
    height: auto;
    width: 100% !important;
    -o-object-fit: cover;
    object-fit: cover;
    transition: none;
  }

  .hotel__slide.swiper-slide-active img {
    opacity: 1;
    height: auto;
    width: 100% !important;
    transition: none;
  }
}

@media (max-width: 900px) {
  #hotel-block {
    padding: 40px 20px;
  }

  .hotel__arrow--next {
    left: initial;
    right: 2%;
  }

  .hotel-inner {
    margin-top: 20px;
  }
}

@media (max-width: 768px) {
  .hotel__slider {
    height: auto;
  }

  .hotel__arrow--next {
    right: 2%;
    left: initial;
  }

  .hotel__arrow--prev {
    left: 2%;
  }
}

@media (max-width: 576px) {
  .hotel-title {
    font-size: 30px;
  }

  .hotel-title span {
    font-size: 40px;
  }
}
