#about-casino {
  background: #0e0d15;
  padding: 160px 200px 160px;
}

#about-casino .container {
  position: relative;
}

.about-blur {
  width: 447px;
  height: 417px;
  border-radius: 50%;
  top: -35px;
  background: rgba(28, 71, 98, 0.4);
  filter: blur(103px);
  position: absolute;
}

.about {
  display: flex;
  justify-content: center;
  position: relative;
}

.about-item:first-child {
  margin-right: 50px;
  margin-top: 80px;
}

.about-title {
  font-family: 'Playfair Display', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 68px;
  line-height: 130%;
  letter-spacing: 0.02em;
  color: #ecbd81;
  margin-bottom: 60px;
  text-align: center;
}

.about-text {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 200%;
  color: #fff;
  max-width: 550px;
  text-align: center;
}

.about-text-content {
  margin-bottom: 40px;
}

.about-blur__casino {
  width: 300px;
  height: 300px;
  top: -33px;
}

@media (max-width: 1560px) {
  #about-casino {
    padding: 100px 200px 160px;
  }
}

@media (max-width: 1400px) {
  .about-item:first-child {
    margin-top: 0;
  }

  .about-title {
    font-size: 36px;
  }

  .about-text {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 200%;
    color: #fff;
    max-width: 300px;
  }
}

@media (max-width: 1350px) {
  .about-img-wrapper img {
    width: 70%;
  }
}

@media (max-width: 1150px) {
  .about-img-wrapper img {
    width: 100%;
  }
}

@media (max-width: 992px) {
  #about-casino {
    padding: 50px 130px;
  }

  .about {
    flex-direction: column;
    align-items: center;
  }

  .about-item:first-child {
    order: 1;
    margin-right: 0;
    margin-top: 20px;
  }

  .about-title {
    margin-bottom: 20px;
  }
}

@media (max-width: 576px) {
  #about-casino {
    padding: 20px;
  }
}

