.popup {
  position: fixed;
  right: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  height: 100vh;
  transition: 1s;
  z-index: 103;
}

.popup-menu {
  display: none;
}

.popup-menu_content {
  position: fixed;
  right: 0;
  width: 80%;
  background-color: #0e0d15;
  height: 100vh;
  display: flex;
  flex-direction: column;
  z-index: 103;
  transform: translateX(-200%);
  transition: 1s;
  padding: 32px;
  padding-top: 130px;
}

.active-menu {
  transform: translateX(0%);
  transition: 1s;
}

.mobile-menu-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.navbar-mobile {
  display: flex;
  flex-direction: column;
}

.mobile-lang-btn.active {
  color: #ecbd81;
}

.close-menu {
  position: absolute;
  top: 4%;
  right: 8%;
}

@media (max-width: 340px) {
  .popup-menu_content {
    padding-top: 50px;
  }

  .play-btn {
    width: 130px;
  }
}

@media (max-height: 450px) {
  .header {
    position: static;
    background-color: #0e0d15;
  }

  .header-logo__text {
    display: none;
  }

  .header-logo__img {
    display: block;
  }

  .burger {
    position: fixed;
    right: 10px;
    background: rgba(14, 13, 21, 0.5);
  }

  .popup-menu_content {
    padding-top: 5px;
  }

  .navbar-mobile {
    margin-top: 0;
  }

  .navbar-mobile a {
    padding: 5px;
    font-size: 12px;
  }

  .lang-mobile-menu {
    margin-top: 5px;
  }

  .header-play-mobile {
    margin-top: 5px;
    padding: 3px 22px;
    font-size: 12px;
  }

  .social-media-contacts_mobile {
    margin-top: 0;
  }

  .mobile-lang-btn {
    font-size: 12px;
  }
}
