.header {
  background: transparent;
  padding: 40px 50px;
  position: fixed;
  z-index: 999;
  width: 100%;
  transition: 0.5s;
}

.header .container {
  position: relative;
}

.header.active {
  background-color: #0e0d15;
}

.header.scroll {
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translateY(-95px);
  transform: translateY(-95px);
  z-index: -500;
  transition: 0.5s;
}

.header-wrapper {
  display: flex;
  align-items: center;
}

.header-logo {
  position: absolute;
}

.header-logo__text {
  display: flex;
  flex-direction: column;
}

.header-logo__text a:hover {
  color: #fff;
}

.header-logo__text.inactive {
  display: none;
}

.header-logo__link {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 160%;
  color: #a4a7ab;
  text-decoration: none;
}

.navbar {
  margin-left: auto;
  margin-right: auto;
}

.nav-link {
  text-decoration: none;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 157%;
  text-align: center;
  letter-spacing: 0.02em;
  color: #fff;
  padding: 30px;
  text-transform: uppercase;
}

.nav-link:hover {
  color: #ecbd81;
}

.header-buttons {
  position: absolute;
  right: 0;
}

.header-buttons-top {
  display: flex;
  align-items: center;
}

.header-buttons-top.d-none {
  display: none;
}

.header-buttons-icons {
  display: none;
}

.header-buttons-icons.active {
  display: flex;
}

.header-icons__link {
  text-decoration: none;
  margin-left: 20px;
}

.lang-btn {
  border: none;
  background: transparent;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 157%;
  color: #a4a7ab;
  padding: 10px;
  text-decoration: none;
}

.lang-btn.active {
  color: #fff;
}

.lang-btn:hover {
  color: #fff;
}

.play-btn {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #0e0d15;
  border: none;
  background: #ecbd81;
  width: 180px;
  padding: 9px 28px;
  margin-left: 40px;
}

.play-btn:hover {
  box-shadow: 4px 4px 43 0 rgba(34, 60, 80, 0.2) inset;
}

.burger {
  display: none;
}

.popup {
  position: fixed;
  right: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  height: 100vh;
  transition: 1s;
  z-index: 103;
}

.popup-menu {
  display: none;
}

.popup-menu_content {
  position: fixed;
  right: 0;
  width: 80%;
  background-color: #0e0d15;
  height: 100vh;
  display: flex;
  flex-direction: column;
  z-index: 103;
  transform: translateX(-200%);
  transition: 1s;
  padding: 32px;
  padding-top: 130px;
}

.active-menu {
  transform: translateX(0%);
  transition: 1s;
}

.mobile-menu-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.navbar-mobile {
  display: flex;
  flex-direction: column;
}

.mobile-lang-btn.active {
  color: #ecbd81;
}

@media (max-width: 1400px) {
  .nav-link {
    padding: 15px;
  }

  .play-btn {
    width: 150px;
  }
}

@media (max-width: 1200px) {
  .header-logo {
    position: static;
  }

  .header-wrapper {
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .navbar {
    display: none;
  }

  .header-buttons {
    position: static;
    display: flex;
    margin-right: 50px;
  }

  .burger {
    position: absolute;
    right: 0;
    display: block;
    cursor: pointer;
  }

  .header {
    z-index: 103;
  }

  .header-buttons-top {
    flex-basis: 100%;
    margin-top: 10px;
  }

  .navbar-mobile {
    display: flex;
    flex-direction: column;
    margin-left: 0;
    margin-top: 24px;
    width: 170px;
  }

  .nav-link {
    text-align: left;
    padding: 0;
    padding: 10px 0;
  }

  .header-play-mobile {
    margin-left: 0;
    width: 165px;
    margin-top: 24px;
  }

  .lang-mobile-menu {
    margin-top: 24px;
  }

  .mobile-adress {
    width: 165px;
    margin-top: 24px;
  }

  .social-media-contacts_mobile {
    position: static;
    width: 190px;
    margin-top: 24px;
  }
}

@media (max-width: 768px) {
  .header {
    padding: 30px;
  }
}

@media (max-width: 576px) {
  .header {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .header {
    position: static;
    background-color: #0e0d15;
  }

  .header-wrapper {
    align-items: baseline;
  }

  .header-logo__text {
    display: none;
  }

  .header-logo__img {
    display: block;
  }

  .header-buttons {
    margin-right: 0;
    width: 100%;
  }

  .header-buttons-top {
    justify-content: space-between;
  }

  .play-btn {
    margin-left: 20px;
  }

  .burger {
    position: fixed;
    right: 10px;
    background: rgba(14, 13, 21, 0.5);
  }
}

@media (max-width: 340px) {
  .popup-menu_content {
    padding-top: 50px;
  }

  .play-btn {
    width: 130px;
  }
}

@media (max-height: 450px) {
  .header {
    position: static;
    background-color: #0e0d15;
  }

  .header-logo__text {
    display: none;
  }

  .header-logo__img {
    display: block;
  }

  .burger {
    position: fixed;
    right: 10px;
    background: rgba(14, 13, 21, 0.5);
  }

  .popup-menu_content {
    padding-top: 5px;
  }

  .navbar-mobile {
    margin-top: 0;
  }

  .navbar-mobile a {
    padding: 5px;
    font-size: 12px;
  }

  .lang-mobile-menu {
    margin-top: 5px;
  }

  .header-play-mobile {
    margin-top: 5px;
    padding: 3px 22px;
    font-size: 12px;
  }

  .social-media-contacts_mobile {
    margin-top: 0;
  }

  .mobile-lang-btn {
    font-size: 12px;
  }
}
