#accordion-block {
  background: #fff;
  margin: 0 auto;
  padding: 160px 0;
}

#accordion-block .container {
  margin: 0 auto;
  max-width: 1200px;
}

.accordiion-block__title {
  font-family: 'Playfair Display', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 68px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #0e0d15;
}

.accordion {
  margin-top: 86px;
  max-width: 1200px;
}

.accordion-title {
  padding: 40px;
  position: relative;
  width: 100%;
  background-color: transparent;
  text-align: left;
  font-family: 'Playfair Display', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 180%;
  letter-spacing: 0.02em;
  font-variant: small-caps;
  color: #0e0d15;
  border-top: none;
  border-bottom: 1px solid #0e0d15;
  border-left: transparent;
  border-right: transparent;
  text-transform: uppercase;
}

.accordion-title.active {
  color: #ecbd81;
  background-color: #0e0d15;
}

.accordion-title__border {
  border-top: 1px solid #0e0d15;
}

.accordion-title::after {
  content: '';
  position: absolute;
  right: 40px;
  bottom: 41%;
  width: 17px;
  height: 17px;
  border: 1px solid;
  border-color: #fff #0e0d15 #0e0d15 #fff;
  transform: rotate(45deg);
  transition: border-color 0.5s ease-in-out, bottom 0.5s ease-in-out;
}

.accordion-description___list {
  padding: 40px;
  padding-top: 0;
}

.accordion-description {
  overflow: hidden;
  height: 0;
}

.accordion-description.active {
  height: auto;
  background-color: #0e0d15;
}

.accordion-description___list-item {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 160%;
  color: #fff;
  margin-bottom: 10px;
}

@media (max-width: 992px) {
  #accordion-block {
    padding-top: 20px;
    padding-bottom: 80px;
  }
}

@media (max-width: 768px) {
  #accordion-block {
    padding: 40px 0;
  }

  .accordiion-block__title {
    font-size: 46px;
  }

  .accordion-title {
    font-size: 18px;
  }
}

@media (max-width: 440px) {
  .accordiion-block__title {
    font-size: 46px;
  }

  .accordion-title {
    font-size: 14px;
  }

  .accordion-description___list-item {
    font-size: 12px;
  }

  .accordion-title::after {
    right: 15px;
  }
}
