#maps-block {
  height: 100vh;
  position: relative;
  background: #0e0d15;
}

.maps {
  width: 100%;
  height: 100vh;
}

.map {
  width: 100%;
  height: 100vh;
}

.maps-form {
  position: absolute;
  max-width: 513px;
  background: #0e0d15;
  top: 160px;
  left: 200px;
  padding: 60px 88px 80px;
  z-index: 105;
}

.maps-form__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.maps-form__title {
  font-family: 'Playfair Display', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 38px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 64px;
}

.maps-form__phone-link {
  text-decoration: none;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 180%;
  text-align: center;
  color: #a4a7ab;
}

.maps-form__adress {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 180%;
  text-align: center;
  color: #a4a7ab;
  margin-top: 20px;
}

.form-media-icons {
  position: static;
  margin-top: 50px;
}

.form-btn {
  width: 338px;
  background: #ecbd81;
  padding: 11px;
  margin-top: 95px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #0e0d15;
  border: none;
  margin-left: 0;
}

.maps-wrapper iframe {
  pointer-events: none;
}

.maps-wrapper iframe.active {
  pointer-events: all;
}

.mobile-map-wrapper {
  display: none;
}

@media (max-width: 900px) {
  .map {
    width: 100%;
    height: 50vh;
  }

  #maps-block {
    height: 50vh;
    position: relative;
  }
}

@media (max-height: 600px) {
  .maps-form {
    padding: 40px 88px 53px;
  }

  .maps-form__title {
    margin-bottom: 20px;
  }

  .form-media-icons {
    margin-top: 16px;
  }

  .form-btn {
    margin-top: 32px;
  }

  #maps-block {
    padding-top: 0;
  }
}

@media (max-height: 900px) {
  .maps-form {
    top: 13px;
  }
}

@media (max-height: 800px) {
  #maps-block {
    padding-top: 105px;
  }

  .maps-form {
    top: 115px;
  }
}

@media (max-width: 1200px) {
  .maps-form {
    display: none;
  }
}

@media (max-height: 450px) {
  #maps-block {
    padding-bottom: 230px;
  }
}
